import windowConstants from "shared/constants/window";
import { isNullOrVoid } from "shared/helpers/dataStructure";

export function getWindowDimensions() {
  const { innerWidth: width, innerHeight: height } = window;
  return {
    width,
    height
  };
}

export function getActiveBreakpoint(width: number) {
  const breakPointsInReverseSortOrder = Array.from(windowConstants.breakpointsMap.keys()).sort((a, b) => b - a);
  const activeBreakpointValue = breakPointsInReverseSortOrder.find(bp => width >= bp);

  if (!isNullOrVoid(activeBreakpointValue)) {
    return windowConstants.breakpointsMap.get(activeBreakpointValue);
  }
}
